import { useState, useEffect } from 'react';
import { EMAIL_PROVIDERS, DEFAULT_CONFIG } from './constants';
import { SyncConfig, SyncStatus, MessageType, EmailData } from './types';
import { syncEmails, checkServiceStatus } from './services/emailApi';
import { bitableService } from './services/bitableApi';

function App() {
  // 
  const [config, setConfig] = useState<SyncConfig>({
    email: '',
    password: '',
    provider: 'lark',
    count: DEFAULT_CONFIG.SYNC_COUNT
  });

  // 
  const [syncStatus, setSyncStatus] = useState<SyncStatus>('idle');
  const [progress, setProgress] = useState(0);
  const [message, setMessage] = useState<{ type: MessageType; text: string } | null>(null);
  const [tableName, setTableName] = useState<string>('');

  // 
  useEffect(() => {
    initializeApp();
  }, []);

  /**
   * 
   */
  const initializeApp = async () => {
    try {
      // 
      const statusResult = await checkServiceStatus();
      if (!statusResult.success) {
        showMessage('error', '');
        return;
      }

      // 
      const initResult = await bitableService.init();
      if (!initResult) {
        showMessage('error', '');
        return;
      }

      // 
      const name = await bitableService.getTableName();
      setTableName(name);

      showMessage('info', '');
    } catch (error: any) {
      console.error(':', error);
      showMessage('error', ': ' + error.message);
    }
  };

  /**
   * 
   */
  const showMessage = (type: MessageType, text: string) => {
    setMessage({ type, text });
    setTimeout(() => setMessage(null), 5000);
  };

  /**
   * 
   */
  const updateConfig = (key: keyof SyncConfig, value: string | number) => {
    setConfig(prev => ({
      ...prev,
      [key]: value
    }));
  };

  /**
   * 
   */
  const validateForm = (): boolean => {
    if (!config.email.trim()) {
      showMessage('error', '');
      return false;
    }

    if (!config.password.trim()) {
      showMessage('error', '');
      return false;
    }

    if (config.count < DEFAULT_CONFIG.MIN_SYNC_COUNT || config.count > DEFAULT_CONFIG.MAX_SYNC_COUNT) {
      showMessage('error', ` ${DEFAULT_CONFIG.MIN_SYNC_COUNT}-${DEFAULT_CONFIG.MAX_SYNC_COUNT} `);
      return false;
    }

    return true;
  };

  /**
   * 
   */
  const handleSync = async () => {
    if (!validateForm()) {
      return;
    }

    setSyncStatus('syncing');
    setProgress(0);
    showMessage('info', '...');

    try {
      // 1: API (30%)
      setProgress(30);
      const emailResult = await syncEmails(config);
      
      if (!emailResult.success || !emailResult.data) {
        throw new Error(emailResult.error || '');
      }

      const emails: EmailData[] = emailResult.data;
      showMessage('info', ` ${emails.length} `);

      // 2:  (60%)
      setProgress(60);
      const tableResult = await bitableService.ensureEmailTable();
      
      if (!tableResult.success) {
        throw new Error(tableResult.message || '');
      }

      // 3:  (90%)
      setProgress(90);
      const addResult = await bitableService.addEmailRecords(emails);
      
      if (!addResult.success) {
        throw new Error(addResult.message || '');
      }

      //  (100%)
      setProgress(100);
      setSyncStatus('success');
      showMessage('success', ` ${addResult.count} `);

    } catch (error: any) {
      console.error(':', error);
      setSyncStatus('error');
      showMessage('error', error.message || '');
    }
  };

  /**
   * 
   */
  const resetSync = () => {
    setSyncStatus('idle');
    setProgress(0);
    setMessage(null);
  };

  return (
    <div className="container">
      <h2 style={{ marginBottom: '24px', textAlign: 'center', color: '#333' }}>
        
      </h2>

      {tableName && (
        <div style={{ marginBottom: '16px', padding: '8px', backgroundColor: '#f0f0f0', borderRadius: '4px' }}>
          <small>: {tableName}</small>
        </div>
      )}

      {/*  */}
      <div className="form-group">
        <label className="form-label"></label>
        <select
          className="form-select"
          value={config.provider}
          onChange={(e) => updateConfig('provider', e.target.value)}
          disabled={syncStatus === 'syncing'}
        >
          {EMAIL_PROVIDERS.map(provider => (
            <option key={provider.value} value={provider.value}>
              {provider.label}
            </option>
          ))}
        </select>
      </div>

      {/*  */}
      <div className="form-group">
        <label className="form-label"></label>
        <input
          type="email"
          className="form-input"
          placeholder=""
          value={config.email}
          onChange={(e) => updateConfig('email', e.target.value)}
          disabled={syncStatus === 'syncing'}
        />
      </div>

      {/*  */}
      <div className="form-group">
        <label className="form-label"></label>
        <input
          type="password"
          className="form-input"
          placeholder=""
          value={config.password}
          onChange={(e) => updateConfig('password', e.target.value)}
          disabled={syncStatus === 'syncing'}
        />
        <small style={{ color: '#666', fontSize: '12px' }}>
          
        </small>
      </div>

      {/*  */}
      <div className="form-group">
        <label className="form-label"></label>
        <input
          type="number"
          className="form-input"
          min={DEFAULT_CONFIG.MIN_SYNC_COUNT}
          max={DEFAULT_CONFIG.MAX_SYNC_COUNT}
          value={config.count}
          onChange={(e) => updateConfig('count', parseInt(e.target.value) || DEFAULT_CONFIG.SYNC_COUNT)}
          disabled={syncStatus === 'syncing'}
        />
        <small style={{ color: '#666', fontSize: '12px' }}>
           {DEFAULT_CONFIG.MAX_SYNC_COUNT} 
        </small>
      </div>

      {/*  */}
      {syncStatus === 'syncing' && (
        <div className="progress-bar">
          <div 
            className="progress-fill" 
            style={{ width: `${progress}%` }}
          />
        </div>
      )}

      {/*  */}
      {message && (
        <div className={`message message-${message.type}`}>
          {message.text}
        </div>
      )}

      {/*  */}
      <div style={{ textAlign: 'center', marginTop: '24px' }}>
        {syncStatus === 'idle' && (
          <button
            className="btn btn-primary"
            onClick={handleSync}
            style={{ width: '100%' }}
          >
            
          </button>
        )}

        {syncStatus === 'syncing' && (
          <button
            className="btn btn-primary"
            disabled
            style={{ width: '100%' }}
          >
            ... ({progress}%)
          </button>
        )}

        {(syncStatus === 'success' || syncStatus === 'error') && (
          <button
            className="btn btn-primary"
            onClick={resetSync}
            style={{ width: '100%' }}
          >
            
          </button>
        )}
      </div>

      {/*  */}
      <div style={{ marginTop: '24px', padding: '16px', backgroundColor: '#f9f9f9', borderRadius: '4px' }}>
        <h4 style={{ marginBottom: '8px', fontSize: '14px' }}></h4>
        <ul style={{ fontSize: '12px', color: '#666', paddingLeft: '16px' }}>
          <li></li>
          <li></li>
          <li></li>
          <li></li>
        </ul>
      </div>
    </div>
  );
}

export default App;